body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(48, 47, 47);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ExperienceTD{
border-radius: 5px;
width: 50%;
border: 2px;
border-style: solid;
border-color: black;
}

.ExperienceBox{
margin: auto;
margin-top: 1vh;
border: 2px;
border-style: solid;
border-color: rgb(32,32,32);
border-radius: 5px;
width: 90%;
height: 50vh;
}

.GitPicture{
box-shadow: 2px 2px 10px black;
border-radius: 50%;
min-width:100px;
min-height:100px;
max-width:200px;
max-height:200px;
width: auto;
height: auto;
margin-top: 2vh;
}

.ExperienceTable{
border-radius: 5px;
background-color: rgb(75, 73, 73);
box-shadow: 2px 2px 10px black;
height: 100%;
width: 100%;
text-align: center;
}

.ExperienceName{
text-align: center;
color:rgb(22, 175, 137);
font-family: Helvetica, sans-serif;
font-size: 24px;
text-shadow: 1px 1px black;
}

.ExperiencePic{
border-radius: 5px;
box-shadow: 1px 1px 5px black;
min-width:100px;
min-height:100px;
max-width:200px;
max-height:200px;
width: auto;
height: auto;
margin-top: 2vh;
}

.GitPicture:hover{
opacity: 0.8;
box-shadow: 3px 3px 15px black;
}

.ExperiencePic:hover{
opacity: 0.8;
box-shadow: 2px 2px 10px black;
}

/* Tablet */
@media (max-width: 1080px) {
.GitPicture{
  box-shadow: 2px 2px 10px black;
  border-radius: 50%;
  max-width:150px;
  max-height:150px;
  width: auto;
  height: auto;
  margin-top: 2vh;
}
.ExperiencePic{
  max-width:150px;
  max-height:150px;
  width: auto;
  height: auto;
  margin-top: 2vh;
}
.ExperienceName{
  color:rgb(22, 175, 137);
  font-family: Helvetica, sans-serif;
  font-size: 18px;
}
}

/* Phone 
@media (orientation: portrait) */
@media (max-width: 667px) {
.GitPicture{
  box-shadow: 2px 2px 10px black;
  border-radius: 50%;
  max-width:100px;
  max-height:100px;
  width: auto;
  height: auto;
  margin-top: 2vh;
}
.ExperiencePic{
  max-width:100px;
  max-height:100px;
  width: auto;
  height: auto;
  margin-top: 2vh;
}
.ExperienceName{
  color:rgb(22, 175, 137);
  font-family: Helvetica, sans-serif;
  font-size: 14px;
}
}